.section span,
.section nav,
.footer {
  display: flex;
  justify-content: center;
}

.section {
  display: flex;
  top: 0;
  left: 0;
  flex-direction: column;
  justify-content: flex-start;
}

.section,
.footer {
  position: fixed;
  z-index: 2;
  width: 100vw;
  height: 100vh;
}

.footer {
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.5));
}

.section nav {
  font-size: 2.5vh;
  font-style: italic;
  color: white;
}

/* Logo */
.logo-root {
  width: 35vh;
  height: 35vh;
}

.logo-s {
  stroke-width: 10px !important;
}

.logo-v {
  stroke-width: 2px !important;
}

/* Caption */
.caption {
  color: white;
  text-align: center;
  font-size: 2.35vh;
}

.caption-wrapper {
  display: inline-flex;
  justify-content: center;
  max-width: 80vw;
}

/* Socials */
.socials-icon,
.footer {
  height: 7vh;
}

.socials-icon {
  width: 5vh;
  margin: 0 1.3vh;
  fill: #9cc1c1;
  transition: fill 250ms;
}

.socials-icon:hover {
  fill: #ccfcfc;
}

.socials-wrapper {
  display: flex;
  justify-content: space-evenly;
}

.nav-wrapper {
  width: fit-content;
  display: flex;
  justify-content: space-between;
}

.nav-item {
  padding: 0 50px;
}

.caption-wrapper,
.nav-wrapper,
.socials-wrapper {
  animation: fade-in;
  animation-duration: 1.25s;
  animation-iteration-count: 1;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 625px) {
  .caption {
    font-size: 2vh;
  }

  .nav-wrapper {
    flex-direction: column;
    align-items: center;
    padding: 0;
  }

  .nav-item {
    padding: 15px 50px;
  }
}

@media screen and (max-height: 600px) {
  .caption {
    font-size: 4.1vh;
  }

  .section nav {
    font-size: 4vh;
  }

  .nav-item {
    padding: 10px 50px;
  }
}
